<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'BarChart',
  data () {
    return {
      myChart: {},
      datas: {}
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.datas) {
        if (item.name === null) {
          item.name = ''
        }
        Arr.push(item.name)
      }
      return Arr
    },
    checkNum: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.checkNum)
      }
      return Arr
    },
    uncheckNum: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push((item.uncheckNum))
      }
      return Arr
    },
    totalCheckNum: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push((item.totalCheckNum))
      }
      return Arr
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /* 获取字段列表 */
    dataHandle () {
      this.datas = this.data
      this.drawLine()
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        grid: {
          left: '40px',
          right: '40px',
          top: '20%',
          containLabel: true
        },
        legend: {
          data: ['斑块数量', '已核查斑块数量', '未核查斑块数量'],
          top: 20,
          right: 30
        },
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            return params[0].name + '<br>' + params[0].seriesName + ':' + params[0].value + '个' + '<br>' + params[1].seriesName + ':' + params[1].value + '个' + '<br>' + params[2].seriesName + ':' + params[2].value + '个'
          }
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData
        },
        yAxis: [
          {
            type: 'value',
            name: '数量/个',
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        dataZoom: [
          {
            type: 'slider',
            show: true
          }
        ],
        series: [
          {
            name: '斑块数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(194, 53, 49)'
              }
            },
            yAxisIndex: 0,
            data: this.totalCheckNum,
            barMaxWidth: 30
          },
          {
            name: '已核查斑块数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(116, 159, 131)'
              }
            },
            yAxisIndex: 0,
            data: this.checkNum,
            barMaxWidth: 30
          },
          {
            name: '未核查斑块数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(47,69,84)'
              }
            },
            yAxisIndex: 0,
            data: this.uncheckNum,
            barMaxWidth: 30
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

</style>
