<template>
  <div id="areaManagement-wrapper">
    <QueryBox @searchSrldId="searchSrldId"></QueryBox>
    <div style="height: calc(100% - 65px)">
      <div style="text-align: left;padding-left: 20px;">
        统计维度：
        <RadioGroup type="button" v-model="analysisType" @on-change="handleChangeType">
          <Radio label="sf">行政区划-核查</Radio>
          <Radio label="sa015">所属部门-核查</Radio>
          <Radio label="uname">巡查人-核查</Radio>
          <Radio label="sf1">行政区划-巡查</Radio>
          <Radio label="sa0151">所属部门-巡查</Radio>
          <Radio label="uname1">巡查人-巡查</Radio>
        </RadioGroup>
      </div>
      <div class="showBox">
        <transition name="fade">
          <div class="sta_box sta_box1" :class="{activeBox: boxstate === 1}">
            <div class="con_box">
              <Form :model="statisticForm" :label-width="90" class="statisticForm" v-if="analysisType === 'sf1' || analysisType === 'sa0151' || analysisType === 'uname1'">
                <FormItem prop="city" label="统计类型：">
                  <i-select v-model.trim="statisticForm.statisticType" style="width: 100%" placeholder="请选择统计类型" @on-change="changeStatisticType">
                    <i-option v-for="item in statisticList" :value="item.value">{{ item.label }}</i-option>
                  </i-select>
                </FormItem>
              </Form>
              <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 1 ? 0 :1" size="small" class="boxBtn"></Button>
              <div class="bigBox" :class="{smallToBig:boxstate === 1}">
                <div class="smallBox" v-if="analysisType === 'sf' || analysisType === 'sa015' || analysisType === 'uname'">
                  <BarChart :data="data1"></BarChart>
                </div>
                <div class="smallBox" v-else-if="analysisType === 'sf1' || analysisType === 'sa0151' || analysisType === 'uname1'">
                  <BarChart2 :data="data1" :type="statisticForm.statisticType"></BarChart2>
                </div>
              </div>
              <Spin size="large" fix v-if="spinShow"></Spin>
            </div>
          </div>
        </transition>
        <div class="sta_box sta_box2" :class="{activeBox: boxstate === 2}">
          <div class="con_box">
            <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 2 ? 0 :2" size="small" class="boxBtn"></Button>
            <div class="bigBox" :class="{smallToBig:boxstate === 2}">
              <div class="smallBox">
                <Table v-if="analysisType === 'sf'" border stripe :loading="loading" :columns="columns2" :data="data1" highlight-row :max-height="tableHeight">
                </Table>
                <Table v-else-if="analysisType === 'sa015'" border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">
                </Table>
                <Table v-else-if="analysisType === 'uname'" border stripe :loading="loading" :columns="columns6" :data="data1" highlight-row :max-height="tableHeight">
                </Table>
                <Table v-else-if="analysisType === 'sf1'" border stripe :loading="loading" :columns="columns4" :data="data1" highlight-row :max-height="tableHeight">
                </Table>
                <Table v-else-if="analysisType === 'sa0151'" border stripe :loading="loading" :columns="columns3" :data="data1" highlight-row :max-height="tableHeight">
                </Table>
                <Table v-else-if="analysisType === 'uname1'" border stripe :loading="loading" :columns="columns5" :data="data1" highlight-row :max-height="tableHeight">
                </Table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QueryBox from './areaQueryBox.vue'
import BarChart from './BarChart.vue'
import BarChart2 from './BarChart2.vue'
import {
  // 统计数据
  listPatrol
} from '@/api/areaManagement'
export default {
  name: 'dataAnalysis',
  data () {
    return {
      /* 查询框返回查询条件 */
      obj: '',
      cl002: '', // 任务名称
      comFPkid: '', // 行政区字段
      // uname: '', // 核查人名称
      deptName: '', // 所属部门
      data1: [],
      /* 表格高度 */
      tableHeight: window.innerHeight - 315,
      // loading
      spinShow: false,
      /* 表格数据 */
      columns6: [
        {
          title: '序号',
          key: 'index',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '巡查人',
          align: 'center',
          key: 'name',
          minWidth: 120
        },
        {
          title: '斑块数量(个)',
          align: 'center',
          key: 'totalCheckNum',
          minWidth: 120
        },
        {
          title: '已核查斑块数量(个)',
          align: 'center',
          key: 'checkNum',
          minWidth: 150
        },
        {
          title: '未核查斑块数量(个)',
          align: 'center',
          key: 'uncheckNum',
          minWidth: 150
        }
      ],
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '所属部门',
          align: 'center',
          key: 'name',
          minWidth: 120
        },
        {
          title: '斑块数量(个)',
          align: 'center',
          key: 'totalCheckNum',
          minWidth: 120
        },
        {
          title: '已核查斑块数量(个)',
          align: 'center',
          key: 'checkNum',
          minWidth: 150
        },
        {
          title: '未核查斑块数量(个)',
          align: 'center',
          key: 'uncheckNum',
          minWidth: 150
        }
      ],
      /* 表格数据 */
      columns2: [
        {
          title: '序号',
          key: 'index',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '行政区划',
          align: 'center',
          key: 'name',
          minWidth: 120
        },
        {
          title: '斑块数量(个)',
          align: 'center',
          key: 'totalCheckNum',
          minWidth: 120
        },
        {
          title: '已核查斑块数量(个)',
          align: 'center',
          key: 'checkNum',
          minWidth: 150
        },
        {
          title: '未核查斑块数量(个)',
          align: 'center',
          key: 'uncheckNum',
          minWidth: 150
        }
      ],
      /* 表格数据 */
      columns3: [
        {
          title: '序号',
          key: 'index',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '所属部门',
          align: 'center',
          key: 'name',
          minWidth: 120
        },
        {
          title: '巡查次数(次)',
          align: 'center',
          key: 'rwcs',
          minWidth: 120
        },
        {
          title: '巡查里程(米)',
          align: 'center',
          key: 'gjlc',
          minWidth: 120
        },
        {
          title: '巡查时长(小时)',
          align: 'center',
          key: 'rwsc',
          minWidth: 120
        }
      ],
      /* 表格数据 */
      columns4: [
        {
          title: '序号',
          key: 'index',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '行政区划',
          align: 'center',
          key: 'name',
          minWidth: 120
        },
        {
          title: '巡查次数(次)',
          align: 'center',
          key: 'rwcs',
          minWidth: 120
        },
        {
          title: '巡查里程(米)',
          align: 'center',
          key: 'gjlc',
          minWidth: 120
        },
        {
          title: '巡查时长(小时)',
          align: 'center',
          key: 'rwsc',
          minWidth: 120
        }
      ],
      /* 表格数据 */
      columns5: [
        {
          title: '序号',
          key: 'index',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '巡查人',
          align: 'center',
          key: 'name',
          minWidth: 120
        },
        {
          title: '巡查次数(次)',
          align: 'center',
          key: 'rwcs',
          minWidth: 120
        },
        {
          title: '巡查里程(米)',
          align: 'center',
          key: 'gjlc',
          minWidth: 120
        },
        {
          title: '巡查时长(小时)',
          align: 'center',
          key: 'rwsc',
          minWidth: 120
        }
      ],
      /* 加载 */
      loading: false,
      // 放大缩小状态
      boxstate: 0,
      // 统计维度
      analysisType: 'sf',
      // 统计
      statisticForm: {
        statisticType: '1'
      },
      // 统计类型列表
      statisticList: [
        {
          label: '巡查次数',
          value: '0'
        },
        {
          label: '巡查里程',
          value: '1'
        },
        {
          label: '巡查时长',
          value: '2'
        }
      ]
    }
  },
  components: {
    QueryBox,
    BarChart,
    BarChart2
  },
  created () {
    this.getTableData()
  },
  methods: {
    /* 查询框 */
    searchSrldId (obj) {
      this.comFPkid = obj.code
      this.deptName = obj.deptName
      // this.uname = obj.uname
      this.getTableData()
    },
    /* 获取统计数据 */
    /* axios: 获取列表数据 */
    getTableData () {
      this.spinShow = true
      this.loading = true
      let groupByName = ''
      if (this.analysisType === 'sf' || this.analysisType === 'sf1') {
        groupByName = 'sf'
      } else if (this.analysisType === 'sa015' || this.analysisType === 'sa0151') {
        groupByName = 'sa015'
      } else if (this.analysisType === 'uname' || this.analysisType === 'uname1') {
        groupByName = 'uname'
      }
      listPatrol({
        data: {
          // st4ScsCl: {
          //   cl002: this.cl002
          // },
          code: this.comFPkid,
          deptName: this.deptName,
          // uname: this.uname,
          groupByName: groupByName
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = []
          if (res.data.data.length > 0) {
            // 根据统计维度值显示表格数据
            if (this.analysisType === 'sf') {
              res.data.data.forEach((item, index) => {
                this.data1.push({
                  name: item.sysCompany.comName,
                  checkNum: item.st4ScsCd.checkNum, // 已核查斑块
                  uncheckNum: item.st4ScsCd.uncheckNum, // 未核查斑块
                  totalCheckNum: item.st4ScsCd.totalCheckNum // 核查斑块总数
                })
              })
            } else if (this.analysisType === 'sf1') {
              res.data.data.forEach((item, index) => {
                this.data1.push({
                  name: item.sysCompany.comName,
                  rwcs: item.rwcs, // 巡查次数
                  gjlc: item.gjlc, // 巡查里程
                  rwsc: item.rwsc // 巡查时长
                })
              })
            } else if (this.analysisType === 'sa015') {
              res.data.data.forEach((item, index) => {
                this.data1.push({
                  name: item.st4SysSa.sa015,
                  checkNum: item.st4ScsCd.checkNum, // 已核查斑块
                  uncheckNum: item.st4ScsCd.uncheckNum, // 未核查斑块
                  totalCheckNum: item.st4ScsCd.totalCheckNum // 核查斑块总数
                })
              })
            } else if (this.analysisType === 'sa0151') {
              res.data.data.forEach((item, index) => {
                this.data1.push({
                  name: item.st4SysSa.sa015,
                  rwcs: item.rwcs, // 巡查次数
                  gjlc: item.gjlc, // 巡查里程
                  rwsc: item.rwsc // 巡查时长
                })
              })
            } else if (this.analysisType === 'uname') {
              res.data.data.forEach((item, index) => {
                this.data1.push({
                  name: item.uname,
                  checkNum: item.st4ScsCd.checkNum, // 已核查斑块
                  uncheckNum: item.st4ScsCd.uncheckNum, // 未核查斑块
                  totalCheckNum: item.st4ScsCd.totalCheckNum // 核查斑块总数
                })
              })
            } else if (this.analysisType === 'uname1') {
              res.data.data.forEach((item, index) => {
                this.data1.push({
                  name: item.uname, // 巡查人
                  rwcs: item.rwcs, // 巡查次数
                  gjlc: item.gjlc, // 巡查里程
                  rwsc: item.rwsc // 巡查时长
                })
              })
            }
          }
          this.spinShow = false
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    handleChangeType (val) {
      this.spinShow = true
      this.loading = true
      this.getTableData()
    },
    // 巡查--统计类型
    changeStatisticType (val) {
      this.spinShow = true
      this.loading = true
      this.getTableData()
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #areaManagement-wrapper{
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .radioBox {
      padding: 0 15px 15px;
      text-align: left;
    }
    .showBox {
      padding: 0 15px;
      height: calc(100% - 32px);
      position: relative;
      .sta_box {
        position: absolute;
        width: 49%;
        overflow: hidden;
        height: 100%;
        padding: 15px;
        background: #fff;
        &.sta_box1 {
          left: 15px;
        }
        &.sta_box2 {
          right: 15px;
        }
        .con_box{
          height: 100%;
          width: 100%;
          border-radius: 10px;
          box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
          position: relative;
          .ivu-table-wrapper {
            border-radius: 10px;
          }
          .boxBtn{
            position: absolute;
            right: 10px;
            top:10px;
            z-index: 80;
          }
          .bigBox{
            width:100%;
            height:100%;
            .smallBox{
              width:100%;
              height:100%;
            }
          }
          .smallToBig {
            .smallBox {
              width: 100%;
            }
          }
        }
      }
    }
    .page_box{
      width: 100%;
      padding: 15px 15px 0;
      text-align: right
    }
    /* box放大 */
    .activeBox{
      width: calc(100% - 15px) !important;
      transition: all 0.4s ease;
      z-index: 999;
    }
    .ivu-radio-group {
      .ivu-radio-wrapper {
        margin-left: 10px;
        border-radius: 4px;
        &:first-child {
          margin-left: 0;
        }
        &.ivu-radio-wrapper-checked {
          background: #2d8cf0;
          color: #fff;
        }
      }
    }
    .statisticForm {
      position: absolute;
      top: 10px;
      right: 60px;
      z-index: 100;
      width: 250px;
    }
  }
</style>
