<template>
    <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'BarChart',
  data () {
    return {
      myChart: {},
      datas: {}
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.datas) {
        if (item.name === null) {
          item.name = ''
        }
        Arr.push(item.name)
      }
      return Arr
    },
    rwcs: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push((item.rwcs))
      }
      return Arr
    },
    gjlc: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push((item.gjlc))
      }
      return Arr
    },
    rwsc: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push((item.rwsc))
      }
      return Arr
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {},
    type: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /* 获取字段列表 */
    dataHandle () {
      this.datas = this.data
      if (this.type === '0') {
        this.drawLine()
      } else if (this.type === '1') {
        this.drawLine1()
      } else if (this.type === '2') {
        this.drawLine2()
      }
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        grid: {
          left: '40px',
          right: '40px',
          top: '20%',
          containLabel: true
        },
        // legend: {
        //   data: ['巡查次数'],
        //   top: 10,
        //   right: 50
        // },
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            return params[0].name + '<br>' + params[0].seriesName + ':' + params[0].value + '次'
          }
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData
        },
        yAxis: [
          {
            type: 'value',
            name: '次数/次',
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        dataZoom: [
          {
            type: 'slider',
            show: true
          }
        ],
        series: [
          {
            name: '巡查次数',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(189,162,154)'
              }
            },
            yAxisIndex: 0,
            data: this.rwcs,
            barMaxWidth: 30
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    drawLine1 () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        grid: {
          left: '40px',
          right: '40px',
          top: '20%',
          containLabel: true
        },
        // legend: {
        //   data: ['巡查里程'],
        //   top: 10,
        //   right: 50
        // },
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            return params[0].name + '<br>' + params[0].seriesName + ':' + params[0].value + '米'
          }
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData
        },
        yAxis: [
          {
            type: 'value',
            name: '里程/米',
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        dataZoom: [
          {
            type: 'slider',
            show: true
          }
        ],
        series: [
          {
            name: '巡查里程',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(212,130,101)'
              }
            },
            yAxisIndex: 0,
            data: this.gjlc,
            barMaxWidth: 30
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    drawLine2 () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        grid: {
          left: '40px',
          right: '40px',
          top: '20%',
          containLabel: true
        },
        // legend: {
        //   data: ['巡查时长'],
        //   top: 10,
        //   right: 50
        // },
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            return params[0].name + '<br>' + params[0].seriesName + ':' + params[0].value + '小时'
          }
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData
        },
        yAxis: [
          {
            type: 'value',
            name: '时长/小时',
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        dataZoom: [
          {
            type: 'slider',
            show: true
          }
        ],
        series: [
          {
            name: '巡查时长',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(202,134,34)'
              }
            },
            yAxisIndex: 0,
            data: this.rwsc,
            barMaxWidth: 30
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

</style>
